  .content .slideshow {
    min-height: initial;
    padding: 0;
    background-attachment: scroll;
    background-position: center top;
  }
  .content .billboard .blackboard {
    margin-top: 0;
    border-radius: 0;
  }
  .content .features {
    padding: 15px;
  }
  .content .features .feature + .feature {
    margin-top: 7px;
  }
  .content .features .feature a.button-link + a.button-link {
    margin-top: 10px;
    margin-left: inherit;
  }
  .content .slideshow #slideshow {
    margin: 0;
    border-radius: 0;
  }
  .content .benefit_list {
    text-align: center;
  }
  .content .benefit_list ol {
    list-style-position: inside;
    padding-left: 0;
  }
  .content .benefit_list ol li > header {
    font-size: 16px;
  }
  .content #testimonial_header {
    text-align: center;
  }
  .content #testimonial_header h1 {
    margin-bottom: 15px;
  }
  .content #testimonial_header div {
    text-align: center;
  }
  .content #curriculum-tour .carousel-inner .item .caption {
    margin-bottom: 25px;
  }
}
@media all and (max-width: 460px) {
  .header.header-xl {
    font-size: 24px;
  }
  .main_wrapper .page_start {
    margin: 10px auto;
  }
  .content #faq .panel-heading {
    padding: 10px 21px 10px 15px;
  }
  .content .lesson_picker_container {
    text-align: center;
  }
  .content .lesson_picker_container .lesson_picker {
    position: inherit;
    left: inherit;
    bottom: inherit;
    max-width: 100%;
  }
  .content .lesson_picker_container .lesson_picker #lesson_select_form select {
    width: 100%;
  }
  .content .lesson_catalog {
    border-radius: 0;
  }
  .content #product_info .nav-tabs li {
    max-width: 33%;
  }
  .footer {
    text-align: center;
  }
  .footer .footer_col {
    text-align: center;
    padding-bottom: 40px;
  }
  .footer .footer-legal p {
