  }
}
@media all and (max-width: 400px) {
  .navbar .navbar-header .navbar-brand {
    font-size: 1.1em;
  }
  .content .billboard {
    background-position: 60% 0;
  }
  .content .billboard .blackboard .tagline {
    font-size: 2.5em;
  }
  .content .slideshow {
    background-position: 23% 0;
  }
  .content .features header {
    font-size: 2em;
  }
  .content #product_info .nav-tabs li a {
    padding: 5px;
  }
  .content #faq .panel-heading {
    padding: 10px 30px 10px 15px;
  }
}
